nav {
	background-color: #333;
	padding: 10px 0;
  }
  
  nav ul {
	list-style: none;
	display: flex;
	justify-content: center;
	margin: 0;
	padding: 0;
  }
  
  nav ul li {
	margin: 0 15px;
  }
  
  nav ul li a {
	color: white;
	text-decoration: none;
	font-size: 18px;
	padding: 10px;
	transition: background-color 0.3s, transform 0.3s;
  }
  
  nav ul li a:hover {
	background-color: #000679;
	transform: scale(1.1);
	border-radius: 5px;
  }
  