/* General body styles */
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #e0f7fa; /* Light blue background */
  color: #333;
  line-height: 1.6;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Main container styles */
.App {
  text-align: center;
  flex: 1;
}

/* Navbar styles */
nav {
  background-color: #00274d; /* Dark blue background */
  padding: 10px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: #ffd700; /* Gold text */
  text-decoration: none;
  font-size: 18px;
  padding: 10px;
  transition: background-color 0.3s, transform 0.3s;
}

nav ul li a:hover {
  background-color: #1a3a7d; /* Lighter blue for hover */
  transform: scale(1.1);
  border-radius: 5px;
}

nav ul li a i {
  margin-right: 5px; /* Add space between icon and text */
}

/* Section styles */
section {
  padding: 60px 20px;
  transition: background-color 0.3s, transform 0.3s;
  max-width: 1200px;
  margin: 20px auto;
  background: white;
  border-radius: 10px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  background-color: #ffffff; /* White background */
}

/* Hero section styles */
#hero {
  background: linear-gradient(to right, #0056b3, #00274d); /* Gradient blue to dark blue */
  color: white;
  padding: 100px 20px;
  text-align: center;
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

#hero h1 {
  font-family: 'Lobster', cursive;
  font-size: 4em;
  margin: 0;
  animation: fadeIn 2s ease-in-out;
}

#hero p {
  font-size: 1.5em;
  margin-top: 10px;
  animation: fadeIn 2.5s ease-in-out;
}

/* Keyframes for fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* About section styles */
#about {
  background-color: #f2f2f2; /* Light gray background */
}

#about h2 {
  font-size: 2.5em;
  color: #0056b3; /* Blue */
  margin-bottom: 20px;
}

#about p {
  font-size: 1.1em;
  margin-bottom: 20px;
}

#about a {
  display: inline-block;
  margin-top: 20px;
  padding: 10px 20px;
  border: 2px solid #ffd700; /* Gold border */
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

#about a:hover {
  background-color: #ffd700;
  color: #00274d; /* Dark blue text */
}

