/* Extracurriculars & Interests section specific styles */
#extracurriculars {
	background-color: #f2f2f2; /* Light gray background */
	color: #333;
	padding: 60px 20px;
	border-radius: 15px;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  #extracurriculars h2 {
	font-size: 2.5em;
	color: #0056b3; /* Blue */
	margin-bottom: 20px;
  }
  
  #extracurriculars p {
	font-size: 1.1em;
  }
  