/* Hero section specific styles */
#hero {
	background: linear-gradient(to right, #0056b3, #00274d); /* Gradient blue to dark blue */
	color: white;
	padding: 100px 20px;
	text-align: center;
	border-radius: 15px;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  #hero h1 {
	font-family: 'Lobster', cursive;
	font-size: 4em;
	margin: 0;
	animation: fadeIn 2s ease-in-out;
  }
  
  #hero p {
	font-size: 1.5em;
	margin-top: 10px;
	animation: fadeIn 2.5s ease-in-out;
  }
  